<script>
  import firebase from 'firebase/app';
  import { db } from '../fireconfig.js';
  import { onMount } from 'svelte';

  import { fade } from 'svelte/transition';
  let showComments = false;

  // global firestore access
  window.db = db;
  //window.firebase = firebase;

  // fetch the data
  export var reviewerData = [];
  window.reviewerData = reviewerData;
  async function fetchData() {
    return await db.collectionGroup('phaseII-herox').get().then(function(querySnapshot) {
      querySnapshot.forEach(function(doc) {
        let dbData = doc.data();
        dbData['id'] = doc.id;
        reviewerData.push(dbData);
      });
      // separate unscored items
      var unscoredData =[];
      reviewerData.forEach((el) => {
        if(el['('+reviewerName+')&total_weighted_score'] == undefined){
          unscoredData.push(el);
        }
      });
      // sort unscored items by presendation order
      unscoredData.sort(function (cardA, cardB) {
        if (cardA.presentation_order > cardB.presentation_order){
      		return 1;
      	} else {
      		return -1;
      	}
      });
      // separate scored items
      var scoredData =[];
      reviewerData.forEach((el) => {
        if(el['('+reviewerName+')&total_weighted_score'] != undefined){
          scoredData.push(el);
        }
      });
      // sort scored items by weighted score
      scoredData.sort(function (cardA, cardB) {
        if (cardA['('+reviewerName+')&total_weighted_score'] < cardB['('+reviewerName+')&total_weighted_score']){
      		return 1;
      	} else {
      		return -1;
      	}
      });
      // update reviewerData: put unscored items first, followed by scored
      reviewerData = unscoredData.concat(scoredData);
      // return the data
      return reviewerData;
    });
  }

  // define reviewer name
  var reviewerName = firebase.auth().currentUser.email.split('@').slice(0);
  let fName = reviewerName[0].split('.')[0];
  fName = fName.charAt(0).toUpperCase() + fName.slice(1);
  let lName = reviewerName[0].split('.')[1];
  if(lName){
    lName = lName.charAt(0).toUpperCase() + lName.slice(1);
  }
  reviewerName = fName+' '+lName;
  if(reviewerName == 'Lett Kathy'){
    reviewerName = 'Kathy Lett';
  }

  //modal variables
  let modalTeam = '';
  let modalTitle = '';
  let modalE2EPdayScore = '';
  let modalE2EOnlineScore = '';
  let modalE2EComments = '';
  let modalImpactPdayScore = '';
  let modalImpactOnlineScore = '';
  let modalImpactComments = '';
  let modalTeamPdayScore = '';
  let modalTeamOnlineScore = '';
  let modalTeamComments = '';
  let modalPdayOverallComments = '';
  let modalOnlineOverallComments = '';

  //edit connector
  window.scoreModal = function scoreIt(el){
    //return object for this entry
    let thisData = reviewerData.find(t => t.id === el);
    modalTeam = thisData.id;
    modalTitle = thisData.submission_title;
    modalE2EPdayScore = thisData['('+reviewerName+')&pday&end-end'] ? thisData['('+reviewerName+')&pday&end-end'] : 0;
    modalE2EOnlineScore = thisData['('+reviewerName+')&online&end-end'] >= 0 ? thisData['('+reviewerName+')&online&end-end'] : 'error';
    modalE2EComments = thisData['('+reviewerName+')&online&end-end&comments'] ? thisData['('+reviewerName+')&online&end-end&comments'] : 'n/a';
    modalImpactPdayScore = thisData['('+reviewerName+')&pday&impact'] ? thisData['('+reviewerName+')&pday&impact'] : 0;
    modalImpactOnlineScore = thisData['('+reviewerName+')&online&impact'] >= 0 ? thisData['('+reviewerName+')&online&impact'] : 'error';
    modalImpactComments = thisData['('+reviewerName+')&online&impact&comments'] ? thisData['('+reviewerName+')&online&impact&comments'] : 'n/a';
    modalTeamPdayScore = thisData['('+reviewerName+')&pday&team'] ? thisData['('+reviewerName+')&pday&team'] : 0;
    modalTeamOnlineScore = thisData['('+reviewerName+')&online&team'] >= 0 ? thisData['('+reviewerName+')&online&team'] : 'error';
    modalTeamComments = thisData['('+reviewerName+')&online&team&comments'] ? thisData['('+reviewerName+')&online&team&comments'] : 'n/a';
    modalPdayOverallComments = thisData['('+reviewerName+')&pday&comments'] ? thisData['('+reviewerName+')&pday&comments'] : '';
    modalOnlineOverallComments = thisData['('+reviewerName+')&online&comments'] ? thisData['('+reviewerName+')&online&comments'] : 'n/a';

    const scoreCard = document.querySelector('.modal');
    //const scoreCardSubmit = document.getElementById('sendScore');
    scoreCard.classList.add('is-active');
    //get y scroll position - for use when closing modal
    var yPx = scrollY;
    //start form at top position
    scoreCard.querySelector('.modal-content').scrollTo(0,0);
    //prevent the body from scrolling while the modal is open
    document.body.style.position = 'fixed';

    //set weighted online score
    let onlineWeightedScore = formatScore(Number.parseFloat((modalE2EOnlineScore/6*.5)+(modalImpactOnlineScore/6*.25)+(modalTeamOnlineScore/6*.25))*100);
    window.onlineE2EScore = modalE2EOnlineScore;
    window.onlineImpactScore = modalImpactOnlineScore;
    window.onlineTeamScore = modalTeamOnlineScore;
    window.onlineWeightedScore = onlineWeightedScore;

    showComments = false;
  }

  //modal operation
  let isOpen = false
  function openModal() {
    isOpen = true
  }
  function close() {
    isOpen = false
    document.body.style.position = '';
  }

  //define scores
  let scoreOptions = [
		{ id: 0, value: 0, text: `choose an option` },
		{ id: 1, value: 1, text: `strongly disagree: 1` },
		{ id: 2, value: 2, text: `disagree: 2` },
		{ id: 3, value: 3, text: `slightly disagree: 3` },
		{ id: 4, value: 4, text: `slightly agree: 4` },
		{ id: 5, value: 5, text: `agree: 5` },
		{ id: 6, value: 6, text: `strongly agree: 6` },
	];

  //submit score
  let scores = [];
  function submitScore(modalTeam){
    //get inputs
    let e2eScore = Number(document.getElementById('e2e').value);
    let impactScore = Number(document.getElementById('impact').value);
    let teamScore = Number(document.getElementById('team').value);
    let pdayComments = document.getElementById('pdayComments').value;
    //calculate weighted scores
    let pdayWeightedScore = formatScore(Number.parseFloat((e2eScore/6*.5)+(impactScore/6*.25)+(teamScore/6*.25))*100);
    let totalWeightedScore = formatScore(Number.parseFloat(((e2eScore+onlineE2EScore)/12*.5)+((impactScore+onlineImpactScore)/12*.25)+((teamScore+onlineTeamScore)/12*.25))*100);
    //define payload
    let e2eField = '('+reviewerName+')&pday&end-end';
    let impactField = '('+reviewerName+')&pday&impact';
    let teamField = '('+reviewerName+')&pday&team';
    let commentsField = '('+reviewerName+')&pday&comments';
    let onlineWeightedField = '('+reviewerName+')&online&weighted_score';
    let pdayWeightedField = '('+reviewerName+')&pday&weighted_score';
    let totalWeightedField = '('+reviewerName+')&total_weighted_score';
    //push data to firestore
    db.collection("phaseII-herox").doc(modalTeam).update({[e2eField]:e2eScore,[impactField]:impactScore,[teamField]:teamScore,[commentsField]:pdayComments,[onlineWeightedField]:onlineWeightedScore,[pdayWeightedField]:pdayWeightedScore,[totalWeightedField]:totalWeightedScore})
    .then(function() {
      //on success:
      //refresh page
      location.reload();
    })
    .catch(function(error) {
      //something went awry
      console.error("Error updating document: ", error);
    });
  }

  const formatScore = (num, decimals) => num.toLocaleString('en-US', {
    minimumFractionDigits: 3,
    maximumFractionDigits: 3,
  });

</script>

<section class="grid grid-cols-8">
  <h1 class="col-span-8 col-start-2 text-3xl text-vto">Lithium-Ion Battery Recycling Prize</h1>
  <h2 class="col-span-8 col-start-2 text-4xl">Participants Day Scoring</h2>
  <p class="test col-span-8 col-start-2">Please set the score for each team</p>
  <div class="vert-spacing col-span-8"><!--whitespace--></div>
  <div id="membersOnly" class="firebase-results col-span-8 grid grid-cols-8">

    {#await fetchData()}
      <div class="w-screen">
        <div class="w-1/3 m-auto text-center">loading...
          <!--<div class="progress-bar bg-dark" role="progressbar" aria-valuenow="98" aria-valuemin="0" aria-valuemax="100" style="width: 98%; animation: 1s ease 0s 1 normal none running animate-positive; opacity: 1;"></div>-->
          <progress class="progress progress-bar" max="100" style="width: 98%; animation: 1s ease 0s 1 normal none running animate-positive; opacity: 1;"></progress>
        </div>
      </div>

    {:then items}
      {#if userRole == 'reviewer' || userRole == 'admin'}
        {#each items as item }
          <div class="card col-start-2 col-span-6 border-light shadow-soft mb-4" data-team="{item.id}" data-scored="{item['('+reviewerName+')&total_weighted_score'] > 0 ? 'yes' : ''}">
            <div class="col-span-8 grid grid-cols-8">
              <p class="text-xs text-gray-500 col-row-1 col-span-4 sm:col-span-6 lg:col-span-7">presentation {item.presentation_order}</p>
              <slot name="trigger" {openModal}>
                <!-- fallback trigger to open the modal -->
                <button class="set-score text-xs text-blue-500 col-row-1 col-start-6 sm:col-start-7 lg:col-start-8 col-span-4 sm:col-span-2 lg:col-span-1" on:click={openModal} on:click={() => scoreModal(item.id)}>set score</button>
              </slot>
              <div class="col-span-8 lg:col-span-5 pt-4">
                <h3 class="text-vto text-2xl col-span-8">{item.id}</h3>
                <p class="col-span-8 sub-title">{item.submission_title}</p>
              </div>
              <div class="col-span-8 lg:col-span-3 lg:col-start-6 text-right pt-4">
                <p class="text-2xl text-gray-600">overall weighted score:
                  {#if item['('+reviewerName+')&total_weighted_score'] }
                    <span class="text-vto">{item['('+reviewerName+')&total_weighted_score']}</span>
                  {:else}
                    <span>--</span>
                  {/if}
                </p>
                <p class="text-lg text-gray-500">weighted presentation score:
                  {#if item['('+reviewerName+')&pday&weighted_score'] }
                    <span>{item['('+reviewerName+')&pday&weighted_score']}</span>
                  {:else}
                    <span>--</span>
                  {/if}
                </p>
                <p class="text-lg text-gray-500">weighted online score:
                  {#if item['('+reviewerName+')&online&end-end'] || item['('+reviewerName+')&online&impact'] || item['('+reviewerName+')&online&team'] }
                    <span>{formatScore(Number.parseFloat((item['('+reviewerName+')&online&end-end']/6*0.5)+(item['('+reviewerName+')&online&impact']/6*0.25)+(item['('+reviewerName+')&online&team']/6*0.25))*100)}</span>
                  {:else}
                    <span>--</span>
                  {/if}
                </p>
              </div>
            </div>
          </div>
        {/each}
      {:else}
      <div class="w-screen">
        <div class="w-1/3 m-auto text-center">
          <p>you don't have access to score presentations</p>
        </div>
      </div>
      {/if}
    {:catch error}
      <p style="color: red">{error.message}</p>
    {/await}

    {#if isOpen}
    <div class="modal w-screen h-screen">
      <div class="modal-content grid grid-cols-8">
        <div class="col-span-8 pt-4">
          <h3 class="text-vto text-3xl font-bold col-span-8" contenteditable="true" bind:innerHTML={modalTeam}></h3>
          <p class="col-span-8 sub-title" contenteditable="true" bind:innerHTML={modalTitle}></p>
        </div>
        <div class="card score-card col-span-8 border-light shadow-soft mt-4 mb-4">
          <div class="grid grid-cols-8">
            <div class="col-span-4 pt-4">
              <h4 class="font-bold text-xl">End-to-End Solution Approach (50%)</h4>
              <p>your online score: <span contenteditable="true" bind:innerHTML={modalE2EOnlineScore}></span></p>
            </div>
            <div class="col-span-3 col-start-6 pt-4 text-right">
              <label for="e2e">presentation score</label>
              <select class="custom-select" name="e2e" id="e2e" dir="rtl">
            		{#each scoreOptions as e2e}
            			<option value={e2e.id} selected="{e2e.id === modalE2EPdayScore}" disabled="{e2e.id === 0}">
            				{e2e.text}
            			</option>
            		{/each}
            	</select>
            </div>
            <div class="col-span-8 pt-4">
              <i class="underline">your online comments:</i>
              <p contenteditable="true" bind:innerHTML={modalE2EComments}></p>
            </div>
          </div>
        </div>
        <div class="card score-card col-span-8 border-light shadow-soft mt-4 mb-4">
          <div class="grid grid-cols-8">
            <div class="col-span-4 pt-4">
              <h4 class="font-bold text-xl">Impact (25%)</h4>
              <p>your online score: <span contenteditable="true" bind:innerHTML={modalImpactOnlineScore}></span></p>
            </div>
            <div class="col-span-3 col-start-6 pt-4 text-right">
              <label for="impact">presentation score</label>
              <select class="custom-select" name="impact" id="impact" dir="rtl">
            		{#each scoreOptions as impact}
            			<option value={impact.id} selected="{impact.id === modalImpactPdayScore}" disabled="{impact.id === 0}">
            				{impact.text}
            			</option>
            		{/each}
            	</select>
            </div>
            <div class="col-span-8 pt-4">
              <i class="underline">your online comments:</i>
              <p contenteditable="true" bind:innerHTML={modalImpactComments}></p>
            </div>
          </div>
        </div>
        <div class="card score-card col-span-8 border-light shadow-soft mt-4 mb-4">
          <div class="grid grid-cols-8">
            <div class="col-span-4 pt-4">
              <h4 class="font-bold text-xl">Team & Partnering (25%)</h4>
              <p>your online score: <span contenteditable="true" bind:innerHTML={modalTeamOnlineScore}></span></p>
            </div>
            <div class="col-span-3 col-start-6 pt-4 text-right">
              <label for="team">presentation score</label>
              <select class="custom-select" name="team" id="team" dir="rtl">
                {#each scoreOptions as team}
                  <option value={team.id} selected="{team.id === modalTeamPdayScore}" disabled="{team.id === 0}">
                    {team.text}
                  </option>
                {/each}
              </select>
            </div>
            <div class="col-span-8 pt-4">
              <i class="underline">your online comments:</i>
              <p contenteditable="true" bind:innerHTML={modalTeamComments}></p>
            </div>
          </div>
        </div>
        <div class="card score-card col-span-8 border-light shadow-soft mt-4 mb-4">
          <div class="grid grid-cols-8">
            <div class="col-span-8 pt-4">
              <h4 class="font-bold text-xl">comments on presentation</h4>
              <textarea class="form-control w-full" placeholder="enter your comments..." id="pdayComments" rows="4" contenteditable="true" bind:innerHTML={modalPdayOverallComments}></textarea>
            </div>
            <div class="col-span-8 pt-2">
              <a class="underline" on:click={() => showComments = !showComments}>view your overall online comments:</a>
                {#if showComments}
                <p contenteditable="true" bind:innerHTML={modalOnlineOverallComments} transition:fade>--</p>
                {/if}
            </div>
          </div>
        </div>
        <div class="col-span-8 mt-4 mb-4">
          <div class="grid grid-cols-8 gap-4">
            <button class="text-base text-gray-800 col-row-1 col-start-1 sm:col-start-5 lg:col-start-7 col-span-4 sm:col-span-2 lg:col-span-1" on:click={close}>cancel</button>
            <button class="text-base text-gray-800 bg-vto col-row-1 col-start-5 sm:col-start-7 lg:col-start-8 col-span-4 sm:col-span-2 lg:col-span-1" on:click={() => submitScore(modalTeam)}>submit</button>
          </div>
        </div>
      </div>
    </div>
    {/if}
  </div>
</section>
