<script>
	// get routes
	import { Router } from "@sveltech/routify";
 	import { routes } from "@sveltech/routify/tmp/routes";

	import { goto } from '@sveltech/routify';

	// get nav
	import Nav from './navbar.svelte';
	// some effects
	import { fade } from 'svelte/transition';
	let loginVisible = true;
	let resetVisible = false;
	// firebase
	import firebase from 'firebase/app';
	import Auth from './fireauth.svelte';
	import 'firebase/firestore';
	import { firebaseConfig } from './fireconfig.js';

  let loginWithEmailPassword;
  let error = null;

  const loginHandler = async event => {
    const { email, password } = event.target.elements;
    try {
      error = null;
      await loginWithEmailPassword(email.value, password.value);
    } catch (err) {
      error = err;
    }
  };

	// reset password
  const resetHandler = async event => {
    const { email } = event.target.elements;
    try {
      error = null;
			firebase.auth().sendPasswordResetEmail(email.value).then(function() {
			  console.log('email sent');
				location.reload();
			}).catch(function(error) {
			  console.log(error);
			});
    } catch (err) {
      error = err;
    }
  };


</script>

<main id="main">

	<Router {routes} />

	<!--<div id="firebaseui-auth-container"></div>-->
	<div class="wrapper">
	  <Auth
	    useRedirect={true}
	    let:user
	    let:loggedIn
	    bind:loginWithEmailPassword
	    let:logout
	  >
    {#if loggedIn}
			<!--{#if userRole == 'reviewer'}
			
			{/if}-->
    {:else}
    <div class="grid grid-cols-12">
			{#if loginVisible}
      <form
        on:submit|preventDefault={loginHandler}
        class="px-8 pt-6 pb-8 card col-start-2 col-span-10 sm:col-start-3 sm:col-span-8 lg:col-start-5 lg:col-span-4 border-light shadow-soft"
				out:fade={{duration: 0}}
				in:fade
      >
        <div class="mb-4">
          <label for="email">Email</label>
          <input
            class="input-field w-full"
            id="email"
            type="email"
            placeholder="email@address.com"
          />
        </div>
        <div class="mb-6">
          <label for="password">Password</label>
          <input
            class="input-field w-full"
            id="password"
            type="password"
            placeholder="••••••••"
          />
        </div>
      {#if error}
        <div transition:fade class="p-2 mb-6 bg-red-300">{error.message}</div>
      {/if}
	      <div class="flex justify-between items-center">
	        <!--<a href="reset" class="text-gray-500 text-xs">forgot password</a>-->
	        <a class="text-gray-500 text-xs" on:click={() => loginVisible = !loginVisible} on:click={() => resetVisible = !resetVisible}>forgot password</a>
	        <button type="submit" class="bg-vto text-gray-200 justify-self-end">Sign In</button>
	      </div>
      </form>
			{/if}
			{#if resetVisible}
				<form id="forgotPassword"
	        on:submit|preventDefault={resetHandler}
	        class="px-8 pt-6 pb-8 card col-start-2 col-span-10 sm:col-start-3 sm:col-span-8 lg:col-start-5 lg:col-span-4 border-light shadow-soft"
					out:fade={{duration: 0}}
					in:fade
	      >
	        <div class="mb-4">
						<p class="text-gray-500">Don’t worry, happens to the best of us.</p>
						<p>Enter your email to recieve a recovery link.</p>
						<br />
	          <label for="email">Email</label>
	          <input
	            class="input-field w-full"
	            id="email"
	            type="email"
	            placeholder="email@address.com"
	          />
	        </div>
	      {#if error}
	        <div transition:fade class="p-2 mb-6 bg-red-300">{error.message}</div>
	      {/if}
		      <div class="flex justify-between items-center">
						<a class="text-gray-500 text-xs" on:click={() => loginVisible = !loginVisible} on:click={() => resetVisible = !resetVisible} >login</a>
		        <button type="submit" class="bg-vto text-gray-200 justify-self-end">Send email</button>
		      </div>
	      </form>
			{/if}
    </div>
    {/if}
  	</Auth>
	</div>

</main>
<style>
	main {
		margin: 0 auto;
	}
	label {
    font-weight: bold;
		color: var(--text-gray-700);
  }
  .input-field {
    border: 3px var(--text-gray-700);
  }
  .input-field:focus {
    shadow-outline: outline-none;
  }
  .wrapper {
		justify-content: center;
		margin: auto;
  }
	#membersOnly .card:hover {
		border-color: var(--amc_vto);
	}
</style>
