<script>
  import firebase from 'firebase/app';
  import { db } from '../fireconfig.js';
  import { onMount } from 'svelte';

  import Viz from '../chart.svelte';

  import { fade } from 'svelte/transition';
  let showComments = false;

  // global firestore access
  window.db = db;

  // define reviewers
  export const rvwr1 = 'Helena Khazdozian';
  export const rvwr2 = 'Kathy Lett';
  export const rvwr3 = 'Kevin Leary';
  export const rvwr4 = 'Lauren Lynch';
  export const rvwr5 = 'Samuel Gillard';

  // fetch the data
  export var scoringData = [];
  window.scoringData = scoringData;
  async function fetchData() {
    return await db.collectionGroup('phaseII-herox').get().then(function(querySnapshot) {
      querySnapshot.forEach(function(doc) {
        let resultsData = doc.data();
        resultsData['id'] = doc.id;
        scoringData.push(resultsData);
      });
      // calculate final avg score for each
      scoringData.forEach((el) => {
        let score1 = (el['('+rvwr1+')&total_weighted_score']) ? Number(el['('+rvwr1+')&total_weighted_score']) : 0;
        let score2 = (el['('+rvwr2+')&total_weighted_score']) ? Number(el['('+rvwr2+')&total_weighted_score']) : 0;
        let score3 = (el['('+rvwr3+')&total_weighted_score']) ? Number(el['('+rvwr3+')&total_weighted_score']) : 0;
        let score4 = (el['('+rvwr4+')&total_weighted_score']) ? Number(el['('+rvwr4+')&total_weighted_score']) : 0;
        let score5 = (el['('+rvwr5+')&total_weighted_score']) ? Number(el['('+rvwr5+')&total_weighted_score']) : 0;
        let finalScore = (score1 + score2 + score3 + score4 + score5)/5;
        el['final_score'] = formatScore(finalScore);
      });

      // sort scored items by weighted score
      scoringData.sort(function (rowA, rowB) {
        if (rowA.final_score < rowB.final_score){
      		return 1;
      	} else {
      		return -1;
      	}
      });

      // return the data
      return scoringData;
    });
  }

  //modal variables
  let modalTeam = '';
  let modalTitle = '';
  let rvwr1OnlineComments = '';
  let rvwr1PdayComments = '';
  let rvwr2OnlineComments = '';
  let rvwr2PdayComments = '';
  let rvwr3OnlineComments = '';
  let rvwr3PdayComments = '';
  let rvwr4OnlineComments = '';
  let rvwr4PdayComments = '';
  let rvwr5OnlineComments = '';
  let rvwr5PdayComments = '';
  let summarizedComments = '';

  //edit connector
  window.commentsModal = function viewComments(el){
    //return object for this entry
    let thisData = scoringData.find(t => t.id === el);
    modalTeam = thisData.id;
    modalTitle = thisData.submission_title;
    summarizedComments = thisData.summarized_comments;
    rvwr1OnlineComments = thisData['('+rvwr1+')&online&comments'] ? thisData['('+rvwr1+')&online&comments'] : 'n/a';
    rvwr1PdayComments = thisData['('+rvwr1+')&pday&comments'] ? thisData['('+rvwr1+')&pday&comments'] : 'n/a';
    rvwr2OnlineComments = thisData['('+rvwr2+')&online&comments'] ? thisData['('+rvwr2+')&online&comments'] : 'n/a';
    rvwr2PdayComments = thisData['('+rvwr2+')&pday&comments'] ? thisData['('+rvwr2+')&pday&comments'] : 'n/a';
    rvwr3OnlineComments = thisData['('+rvwr3+')&online&comments'] ? thisData['('+rvwr3+')&online&comments'] : 'n/a';
    rvwr3PdayComments = thisData['('+rvwr3+')&pday&comments'] ? thisData['('+rvwr3+')&pday&comments'] : 'n/a';
    rvwr4OnlineComments = thisData['('+rvwr4+')&online&comments'] ? thisData['('+rvwr4+')&online&comments'] : 'n/a';
    rvwr4PdayComments = thisData['('+rvwr4+')&pday&comments'] ? thisData['('+rvwr4+')&pday&comments'] : 'n/a';
    rvwr5OnlineComments = thisData['('+rvwr5+')&online&comments'] ? thisData['('+rvwr5+')&online&comments'] : 'n/a';
    rvwr5PdayComments = thisData['('+rvwr5+')&pday&comments'] ? thisData['('+rvwr5+')&pday&comments'] : 'n/a';

    const commentCard = document.querySelector('.modal');
    commentCard.classList.add('is-active');
    //get y scroll position - for use when closing modal
    var yPx = scrollY;
    //start form at top position
    commentCard.querySelector('.modal-content').scrollTo(0,0);
    //prevent the body from scrolling while the modal is open
    document.body.style.position = 'fixed';

    showComments = false;
  }

  //modal operation
  let isOpen = false
  function openModal() {
    isOpen = true
  }
  function close() {
    isOpen = false
    document.body.style.position = '';
  }

  const formatScore = (num, decimals) => num.toLocaleString('en-US', {
    minimumFractionDigits: 3,
    maximumFractionDigits: 3,
  });

  function updateComment(modalTeam){
    //get inputs
    let summarizedComments = document.getElementById('summarizedComments').value;
    //push data to firestore
    db.collection("phaseII-herox").doc(modalTeam).update({'summarized_comments':summarizedComments})
    .then(function() {
      //on success:
      //refresh page
      location.reload();
    })
    .catch(function(error) {
      //something went awry
      console.error("Error updating document: ", error);
    });
  }

</script>

<section class="grid grid-cols-8">
  <h1 class="col-span-8 col-start-2 text-3xl text-vto">Lithium-Ion Battery Recycling Prize</h1>
  <h2 class="col-span-8 col-start-2 text-4xl">Participants Day</h2>
  <p class="test col-span-8 col-start-2">Results Table</p>
  <div class="vert-spacing col-span-8"><!--whitespace--></div>
  <div id="membersOnly" class="pl-8 pr-8 col-span-8 grid grid-cols-8">

    {#await fetchData()}
      <div class="w-screen">
        <div class="w-1/3 m-auto text-center">loading...
          <!--<div class="progress-bar bg-dark" role="progressbar" aria-valuenow="98" aria-valuemin="0" aria-valuemax="100" style="width: 98%; animation: 1s ease 0s 1 normal none running animate-positive; opacity: 1;"></div>-->
          <progress class="progress progress-bar" max="100" style="width: 98%; animation: 500ms ease 0s 1 normal none running animate-positive; opacity: 1;"></progress>
        </div>
      </div>

    {:then items}
      <table class="table table-auto col-span-8">
        <thead>
          <tr class="border-b-4 border-solid border-gray-400">
            <th class="bg-gray-700 text-white">Team</th>
            <th class="bg-gray-700 text-white">Title</th>
            <th class="bg-gray-700 text-white">Helena</th>
            <th class="bg-gray-700 text-white">Kathy</th>
            <th class="bg-gray-700 text-white">Kevin</th>
            <th class="bg-gray-700 text-white">Lauren</th>
            <th class="bg-gray-700 text-white">Samm</th>
            <th class="bg-gray-700 text-white">Final</th>
          </tr>
        </thead>
        <tbody>

        {#each items as item }
          <tr>
            <td rowspan="3" class="text-vto font-bold text-xl border-b-2 border-solid border-gray-400 align-top">{item.id}</td>
            <td>{item.submission_title}</td>
            <td class="text-sm"
              class:text-teal-500={item['('+rvwr1+')&total_weighted_score']>66.33}
              class:text-yellow-600={item['('+rvwr1+')&total_weighted_score']<=66.33 && item['('+rvwr1+')&total_weighted_score']>33.33}
              class:text-pink-500={item['('+rvwr1+')&total_weighted_score']<=33.33}
            >
              {item['('+rvwr1+')&total_weighted_score']}
            </td>
            <td class="text-sm"
              class:text-teal-500={item['('+rvwr2+')&total_weighted_score']>66.33}
              class:text-yellow-600={item['('+rvwr2+')&total_weighted_score']<=66.33 && item['('+rvwr2+')&total_weighted_score']>33.33}
              class:text-pink-500={item['('+rvwr2+')&total_weighted_score']<=33.33}
            >
              {item['('+rvwr2+')&total_weighted_score']}
            </td>
            <td class="text-sm"
              class:text-teal-500={item['('+rvwr3+')&total_weighted_score']>66.33}
              class:text-yellow-600={item['('+rvwr3+')&total_weighted_score']<=66.33 && item['('+rvwr3+')&total_weighted_score']>33.33}
              class:text-pink-500={item['('+rvwr3+')&total_weighted_score']<=33.33}
            >
              {item['('+rvwr3+')&total_weighted_score']}
            </td>
            <td class="text-sm"
              class:text-teal-500={item['('+rvwr4+')&total_weighted_score']>66.33}
              class:text-yellow-600={item['('+rvwr4+')&total_weighted_score']<=66.33 && item['('+rvwr4+')&total_weighted_score']>33.33}
              class:text-pink-500={item['('+rvwr4+')&total_weighted_score']<=33.33}
            >
              {item['('+rvwr4+')&total_weighted_score']}
            </td>
            <td class="text-sm"
              class:text-teal-500={item['('+rvwr5+')&total_weighted_score']>66.33}
              class:text-yellow-600={item['('+rvwr5+')&total_weighted_score']<=66.33 && item['('+rvwr5+')&total_weighted_score']>33.33}
              class:text-pink-500={item['('+rvwr5+')&total_weighted_score']<=33.33}
            >
              {item['('+rvwr5+')&total_weighted_score']}
            </td>
            <td class="text-vto font-bold text-xl">{item.final_score}</td>
          </tr>
          <tr class="">
            <td colspan="6" class="text-sm shadow-inset comments-box">{item.summarized_comments}</td>
            <td class=""><button class="text-xs text-blue-500 col-row-1 col-start-6 sm:col-start-7 lg:col-start-8 col-span-4 sm:col-span-2 lg:col-span-1" on:click={openModal} on:click={() => commentsModal(item.id)}>view / edit comments</button></td>
          </tr>
          <tr>
            <td colspan="7" class="border-b-2 border-b-solid border-gray-400"></td>
          </tr>
        {/each}
        </tbody>
      </table>

    {:catch error}
      <p style="color: red">{error.message}</p>
    {/await}

    {#if isOpen}
    <div class="modal w-screen h-screen">
      <div class="modal-content grid grid-cols-8">
        <div class="col-span-8 pt-4">
          <h3 class="text-vto text-3xl font-bold col-span-8" contenteditable="true" bind:innerHTML={modalTeam}></h3>
          <p class="col-span-8 sub-title" contenteditable="true" bind:innerHTML={modalTitle}></p>
        </div>
        <div class="card score-card col-span-8 border-light shadow-soft mt-4 mb-4">
          <div class="grid grid-cols-8">
            <div class="col-span-8 pt-4">
              <h4 class="font-bold text-xl">summarized comments</h4>
              <textarea class="form-control w-full" placeholder="enter your comments..." rows="10" id="summarizedComments" contenteditable="true" bind:innerHTML={summarizedComments} disabled={userRole=='reviewer'}></textarea>
            </div>
          </div>
        </div>
        <div class="card score-card col-span-8 border-light shadow-soft mt-4 mb-4">
          <div class="grid grid-cols-8">
            <div class="col-span-8 pt-4">
              <h4 class="font-bold text-lg">Online Comments</h4>
              <p contenteditable="true" bind:innerHTML={rvwr1OnlineComments}></p>
              <hr />
              <p contenteditable="true" bind:innerHTML={rvwr2OnlineComments}></p>
              <hr />
              <p contenteditable="true" bind:innerHTML={rvwr3OnlineComments}></p>
              <hr />
              <p contenteditable="true" bind:innerHTML={rvwr4OnlineComments}></p>
              <hr />
              <p contenteditable="true" bind:innerHTML={rvwr5OnlineComments}></p>
              <hr />
            </div>
          </div>
        </div>
        <div class="card score-card col-span-8 border-light shadow-soft mt-4 mb-4">
          <div class="grid grid-cols-8">
            <div class="col-span-8 pt-4">
              <h4 class="font-bold text-lg">Presentation Day Comments</h4>
              <p contenteditable="true" bind:innerHTML={rvwr1PdayComments}></p>
              <hr />
              <p contenteditable="true" bind:innerHTML={rvwr2PdayComments}></p>
              <hr />
              <p contenteditable="true" bind:innerHTML={rvwr3PdayComments}></p>
              <hr />
              <p contenteditable="true" bind:innerHTML={rvwr4PdayComments}></p>
              <hr />
              <p contenteditable="true" bind:innerHTML={rvwr5PdayComments}></p>
              <hr />
            </div>
          </div>
        </div>
        <div class="col-span-8 mt-4 mb-4">
          <div class="grid grid-cols-8 gap-4">
            <button class="text-base text-gray-800 col-row-1 col-start-1 sm:col-start-5 lg:col-start-7 col-span-4 sm:col-span-2 lg:col-span-1" on:click={close}>cancel</button>
            <button class="text-base text-gray-800 bg-vto col-row-1 col-start-5 sm:col-start-7 lg:col-start-8 col-span-4 sm:col-span-2 lg:col-span-1" on:click={() => updateComment(modalTeam)}>submit</button>
          </div>
        </div>
      </div>
    </div>
    {/if}
  </div>

  <!--<Viz/>-->

</section>

