<script>
	import Auth from './fireauth.svelte';

  // Get IE or Edge browser version - assign polyfill for lack of modern CSS support
  var version = detectIE();
  if (version === 11) {
    document.getElementById("new-notification").classList.add("ie");
  }
  /* detect IE */
  function detectIE() {
    var ua = window.navigator.userAgent;
    //IE 11
    //ua = 'Mozilla/5.0 (Windows NT 6.3; Trident/7.0; rv:11.0) like Gecko';
    var msie = ua.indexOf("MSIE ");
      if (msie > 0) {
        // IE 10 or older => return version number
        return parseInt(ua.substring(msie + 5, ua.indexOf(".", msie)), 10);
    }
    var trident = ua.indexOf("Trident/");
    if (trident > 0) {
      // IE 11 => return version number
      var rv = ua.indexOf("rv:");
      return parseInt(ua.substring(rv + 3, ua.indexOf(".", rv)), 10);
    }
    // other browser
      return false;
  }

	import { url, isActive } from "@sveltech/routify";

	const links = [
		["./index", "score table"],
		["./review", "set scores"],
		//["./dashboard", "test"]
	]
</script>
<Auth
  useRedirect={true}
  let:user
  let:loggedIn
  let:logout
>
<nav id="simple-primary-navbar" class="navbar is-primary is-fixed-top shadow-soft" role="navigation" aria-label="main navigation">
  <div class="navbar-brand has-text-weight-bold">
    <a class="navbar-item amc-home" href="/">
      <img src="img/american-made-challenges-inverse-logo.png" alt="App Home"/>
    </a>
  </div>
	{#if loggedIn}
  <div class="navbar-item auth-status flex flex-row items-center text-amc_light_blue">
		{#each links as [path, name]}
		{#if name == 'score table'}
			<a href="/" class:selected={$isActive(path)} class="text-amc_light_blue nav-item">{name}</a>
		{:else}
			<a href={$url(path)} class:selected={$isActive(path)} class="text-amc_light_blue nav-item">{name}</a>
		{/if}
		{/each}
		<div class="user-name text-xs mx-8">
			<p>{user.email}</p>
		</div>
    <button role="button" aria-label="sign out" on:click={logout} href="javascript:void(0)" class="text-gray-200 shadow-softer mb-0">sign out</button>
  </div>
	{/if}
</nav>
</Auth>

